import cat from './cat.jpg';
import './App.css';
import { useState } from 'react'

function App() {
  const pl = 'DAJ   PROJEKT'
  const en = 'GIMME   PROJECT'
  const [ position, setPosition ] = useState('en')
  const [ text, setText ] = useState(en)
  const clicker = () => {
    setPosition(position === 'en' ? 'pl' : 'en')
    setText(position=== 'en'? pl : en)
  }
  return (
    <div className="App">
      <div className='image'>
        <p className='content'>
          {text}
        </p>
        <img src={cat} />
      </div>
      <div className="language-toggler" onClick={()=>clicker()}>
        <div className="language-tag" >EN</div>
        <div className="button-field">
          <div className={"language-button "+position}></div>
        </div>
        <div className="language-tag" >PL</div>
      </div>
    </div>
  );
}

export default App;
